*
{
    font-family: "Red Hat Display", sans-serif;
    margin: 0;
    padding: 0;
}
/**---    ADMIN SECTION    ---**/
/*-----------------------------*/
/**   ADMIN EDITION SECTION   **/
.admin,
.user,
.new
{
    display: flex;
}

.admin-container,
.user-container,
.new-container
{
    flex-basis: 80%;
}

/**---    SIDEBAR SECTION    ---**/
/*-------------------------------*/
/**   SIDEBAR EDITION SECTION   **/
.sidebar
{
    flex-basis: 20%;
    border-right: 0.5px solid rgb(230, 227, 227);
    min-height: 100vh;
}

.sidebar a
{
    text-decoration: none;
}

.top
{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.logo h1
{
    color: #000;
    font-size: 25px;
}

.logo img
{
    width: 35px;
    height: 35px;
    margin-right: 5px;
}

.center
{
    padding-left: 10px;
}

.center ul
{
    list-style: none;
    padding: 0;
    margin: 0;
}

.title
{
    font-size: 10px;
    font-weight: bold;
    color: #999;
    margin-top: 15px;
    margin-bottom: 5px;
}

.center ul li
{
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
}

.center ul li:hover
{
    background-color: #e9ffe8;
}

.center ul li span
{
    font-size: 13px;
    font-weight: 600;
    color: #888;
    margin-left: 10px;
}

.icon
{
    color: #47B850;
}

.bottom
{
    display: flex;
    align-items: center;
    margin: 30px;
}

.color-option
{
    width: 20px;
    height: 20px;
    border-radius: 5px;
    border: 0.5px solid #1E1E1E;
    cursor: pointer;
    margin-right: 5px;
}

.color-option:nth-child(1)
{
    background-color: whitesmoke;
}

.color-option:nth-child(2)
{
    background-color: #333;
}


/**---    NAVBAR SECTION    ---**/
/*------------------------------*/
/**   NAVBAR EDITION SECTION   **/
.admin-navbar
{
    height: 50px;
    padding: 0 30px;
    border-bottom: 0.5px solid rgb(231, 228, 228);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: #1E1E1E;
}

.wrapper
{
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.search
{
    display: flex;
    align-items: center;
    border: 0.5px solid lightgrey;
    padding: 3px;
}

.search input
{
    border: none;
    outline: none;
    background: transparent;
}

.search input::placeholder
{
    font-size: 12px;
    color: rgba(30, 30, 30, 0.55);
}

.items
{
    display: flex;
    align-items: center;
}

.item
{
    display: flex;
    align-items: center;
    margin-right: 15px;
    position: relative;
}

.item svg
{
    font-size: 20px;
}

.nav-icon:hover
{
    color: #5DC9F2;
}

.avatar
{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.counter
{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    font-weight: bold;
    position: absolute;
    top: -5px;
    right: -5px;
}

/**---    WIDGET SECTION    ---**/
/*------------------------------*/
/**   WIDGET EDITION SECTION   **/
.widgets
{
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 20px;
    gap: 20px;
}

.widgets a
{
    display: flex;
    width: 100%;
    justify-content: space-between;
    text-decoration: none;
}

.widget
{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    cursor: pointer;
    height: 100px;
}

.widget-left,
.widget-right
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.widget-right
{
    align-items: flex-end;
}

.widget-left-title
{
    font-size: 14px;
    font-weight: bold;
    color: gray;
}

.widget-left-counter
{
    font-size: 25px;
    font-weight: bold;
    color: #333;
}

.widget-left-link
{
    font-size: 12px;
    font-weight: bold;
    color: #47B850;
    border-bottom: 0.1px solid #47B850;
}

.widget-right-percentage
{
    display: flex;
    align-items: center;
    font-size: 14px;
}

.widget-right-percentage-positive
{
    color: #47B850;
}

.widget-right-percentage-negative
{
    color: red;
}

.widget-right-percentage svg
{
    font-size: 20px;
}

.widget-right-icon svg
{
    font-size: 24px;
    padding: 5px;
    background-color: rgba(128, 9, 128, 0.5);
    border-radius: 5px;
    align-items: flex-end;
}

/**---    CHART SECTION    ---**/
/*-----------------------------*/
/**   CHART EDITION SECTION   **/
.charts
{
    display: flex;
    padding: 5px 20px;
    gap: 20px;
}

.chart
{
    flex-basis: 65%;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
}

.chart-title
{
    margin-bottom: 20px;
}
.chart-title h1
{
    font-size: 16px;
    font-weight: 500;
    color: gray;
}

/**---    FEATURED SECTION    ---**/
/*--------------------------------*/
/**   FEATURED EDITION SECTION   **/
.featured
{
    flex-basis: 35%;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    cursor: pointer;
}

.featured-top
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: gray;
}

.featured-top-title
{
    font-size: 16px;
    font-weight: 500;
}

.featured-bottom
{
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    gap: 15px;
}

.featured-bottom p
{
    margin: 0;
}

.featured-bottom-chart
{
    width: 100px;
    height: 100px;
}

.featured-bottom-title
{
    font-weight: 500;
    color: gray;
}

.featured-bottom-amount
{
    font-size: 30px;
}

.featured-bottom-description
{
    font-size: 12px;
    color: gray;
    font-weight: 300;
    text-align: center;
}

.featured-bottom-summary
{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.featured-bottom-summary-item
{
    align-items: center;
    text-align: center;
    justify-content: center;
}

.featured-bottom-summary-item-title
{
    font-size: 14px;
    color: gray;
}

.featured-bottom-summary-item-result
{
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 14px;
}

.positive
{
    color: #47B850;
}

.negative
{
    color: red;
}

.featured-bottom-summary-item-result svg
{
    font-size: 20px;
}

/**---    LIST SECTION    ---**/
/*----------------------------*/
/**   LIST EDITION SECTION   **/
.list-container
{
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    margin: 20px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    cursor: pointer;
}

.list-container-title
{
    font-weight: 500;
    color: gray;
    margin-bottom: 15px;
}

.table th
{
    font-size: 15px;
    font-weight: 800;
}

.table th,
.table td
{
    font-family: "Red Hat Display", sans-serif;
}

.table-cell-status
{
    padding: 5px;
    border-radius: 5px;
    color: goldenrod;
    background-color: rgba(218, 165, 32, 0.2);
}

.table-cell-status-Entregado
{
    color: green;
    background-color: rgba(0, 128, 0, 0.2);
}

.table-cell-status-Pendiente
{
    color: crimson;
    background-color: rgba(255, 0, 0, 0.2);
}









