/*---------------------------------*/
/**   DATATABLE EDITION SECTION   **/
.datatable
{
    width: 95%;
    padding: 20px;
}

.datatable h1
{
    font-size: 20px;
    font-weight: 600;
    color: gray;
}

.datatable-top
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.datatable-options
{
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 5px;
    border-radius: 5px;
}

.datatable-options button
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    outline: none;
}

.datatable-button svg
{
    width: 15px;
    height: 15px;
}

.create
{
    background: rgba(0, 0, 139, 0.2);
    color: darkblue;
    border: 1px solid rgba(0, 0, 139, 0.5);
    cursor: pointer;
}

.create:hover
{
    background: rgba(0, 0, 139, 0.5);
    color: #FFF;
}

.delete
{
    background: rgba(255, 0, 0, 0.2);
    color: crimson;
    border: 1px solid rgba(255, 0, 0, 0.5);
    cursor: pointer;
}

.delete:hover
{
    background: rgba(255, 0, 0, 0.5);
    color: #FFF;
}

.upload
{
    cursor: pointer;
}

.upload:hover
{
    background: #1E1E1E;
    color: #FFF;
}

.user-table-cell-status
{
    padding: 5px;
    border-radius: 5px;
    width: 70%;
    text-align: center;
    color: goldenrod;
    background-color: rgba(218, 165, 32, 0.2);
}

.user-table-cell-status-active
{
    color: green;
    background-color: rgba(0, 128, 0, 0.2);
}

.user-table-cell-status-inactive
{
    color: crimson;
    background-color: rgba(255, 0, 0, 0.2);
}

.cell-action
{
    display: flex;
    align-items: center;
    gap: 15px;
}

.edit-button
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    padding: 2px 5px;
    border-radius: 5px;
    color: darkblue;
    border: 1px dotted rgba(0, 0, 139, 0.5);
    cursor: pointer;
}

.edit-button svg
{
    width: 15px;
    height: 15px;
}

.edit-button:hover
{
    background-color: rgba(0, 0, 139, 0.2);
}

.delete-button
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    padding: 2px 5px;
    border-radius: 5px;
    color: crimson;
    border: 1px dotted rgba(255, 0, 0, 0.5);
    cursor: pointer;
}

.delete-button svg
{
    width: 15px;
    height: 15px;
}

.delete-button:hover
{
    background-color: rgba(255, 0, 0, 0.2);
}

