.placeorder-info
{
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 20px 100px;
}

.placeorder-top
{
    background-color: rgba(71, 184, 80, 0.6);
    padding: 40px 100px;
}

.placeorder-top h5
{
    font-weight: 500;
}

.placeorder-container
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
    background-color: #FFF;
    gap: 30px;
}

.placeorder-left
{
    flex-basis: 60%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.placeorder-left-products-title,
.placeorder-left-address-title
{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.placeorder-right
{
    flex-basis: 40%;
}

.placeorder-left img
{
    width: 400px;
    height: 200px;
    object-fit: cover;
}

.placeorder-right
{
    align-self: flex-start;
}

.placeorder-left,
.placeorder-right
{
    border: 1px solid #47B850;
    border-radius: 20px;
    padding: 20px;
}

.placeorder-right
{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.placeorder-right-info
{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.placeorder-right-product,
.placeorder-right-shipping
{
    display: flex;
    justify-content: space-between;
}

.placeorder-right-product h4,
.placeorder-right-shipping h4
{
    font-weight: 500;
}