.shipping-address
{
    position: absolute;
    background-color: #FFF;
    left: 0;
    right: 0;
    margin: 30px 130px;
    border-radius: 10px;
    height: 350px;
    border: 1px solid #47B850;
    z-index: 1;
    padding: 40px;
}

.shipping-close
{
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    background-color: transparent;
    padding: 10px 20px;
    font-size: 32px;
    font-weight: 800;
    color: #999999;
}

.shipping-container
{
    width: 100%;
}

.shipping-form
{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.shipping-input
{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.shipping-input input
{
    margin-bottom: 10px;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    color: #000;
    padding: 5px 5px 5px 15px;

    border-radius: 30px;
    border: 1px solid #47B850;
    transition: all 0.5s ease;
}

.shipping-form-info
{
    display: flex;
    justify-content: space-between;
    gap: 30px;
}

.shipping-form button
{
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 400;
    font-size: 16px;
    width: 240px;
    height: 45px;
    margin-top: 20px;
    cursor: pointer;

    /* LinearBG */
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
    border-radius: 30px;
    border: none;
    color: #FFF;
    transition: all 0.3s ease;
}