/**   FOOTER EDITION   **/
.footer
{
    background: #1E1E1E;
    color: #FFF;
    padding: 30px 130px;
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.footer-first-section
{
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 5%;
}

.footer-second-section
{
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
}

.footer-second-section p
{
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 0;
}

.footer-third-section
{
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: left;
}

.footer-fourth-section
{
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.footer-third-section button,
.footer-fourth-section button
{
    font-family: "Red Hat Display", sans-serif;
    background: transparent;
    border: none;
    color: #FFF;
    cursor: pointer;
    font-size: 14px;
    text-align: left;
    justify-content: left;
}

.footer-third-section button:hover,
.footer-fourth-section button:hover
{
    color: #47B850;
}

.logo,
.logo-footer
{
    display: flex;
    align-items: center;
    height: 30px;
    cursor: pointer;
    text-decoration: none;
}

.logo img,
.logo-footer img
{
    height: 150%;
    margin-right: 10px;
}

.logo h1
{
    margin-right: auto;
    color: #FFF;
    font-weight: 700;
    font-size: 28px;
    background: none;
}

.logo-footer h1
{
    margin-right: auto;
    color: #FFF;
    font-weight: 700;
    font-size: 28px;
    background: none;
}

.footer-first-section a
{
    text-decoration: none;
}

.page-sign
{
    display: flex;
    font-family: "Red Hat Display", sans-serif;
    font-size: 12px;
    background: #a4a4a4;
    color: #1E1E1E;
    cursor: pointer;
    width: 100%;
    height: 3rem;
    align-items: center;
    justify-content: space-between;
    padding: 0 130px;
}

