.hero-left
{
    display: flex;
    align-items: center;
    height: 700px;
    background-color: #FFF;
    position: relative;
    background-size: cover;
    background-image: url("https://i.imgur.com/auzVLlb.jpg");
}

.hero-container
{
    max-width: 544px;
    margin-left: 130px;
    z-index: 1;
}

.hero-left::before
{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.45);
}

.hero-container img
{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.hero-text h1, .hero-text h5, .hero-text h6
{
    margin-bottom: 10px;
    margin-top: 10px;
}

.hero-text h1
{
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 700;
    font-size: 64px;
}

.hero-text h5
{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 20px;
}

.hero-text h6
{
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
}

.hero-container button
{
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 400;
    font-size: 16px;
    width: 240px;
    height: 45px;
    margin-top: 20px;
    cursor: pointer;

    /* LinearBG */
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
    border-radius: 30px;
    border: none;
    color: #FFF;
    transition: all 0.3s ease;
}

.hero-container button:hover
{
    background: #FFF;
    color: #47B850;
    border: 1px solid #47B850;
}

.hero-shop
{
    display: flex;
    align-items: center;
    height: 400px;
}