.category-option-banner
{
    margin-top: 40px;
    padding: 30px 130px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}

.category-option-left,
.category-option-right
{
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 40px 30px;
    border-radius: 10px;
    gap: 5px;
    color: #FFF;
}

.category-option-left
{
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
}

.category-option-right
{
    background: linear-gradient(90deg, #47B850 0%, #5DC9F2 100%);
}

.category-option-left button,
.category-option-right button
{
    width: 240px;
    height: 45px;
    border-radius: 30px;
    border: none;
    background-color: #FFF;
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 400;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.category-option-left button:hover,
.category-option-right button:hover
{
    scale: 1.1;
}

.category-option-left h2,
.category-option-left h4,
.category-option-right h2,
.category-option-right h4
{
    width: 50%;
}




