.new-top,
.new-bottom
{
    display: flex;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.5);
    padding: 10px;
    margin: 20px;
}

.new-top h1
{
    color: lightgrey;
    font-size: 20px;
}

.left
{
    flex-basis: 40%;
    text-align: center;
}

.left img
{
    margin-top: 30px;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
}

.right
{
    flex-basis: 60%;
}

.right form
{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.form-input
{
    width: 40%;
    padding: 10px;
}

.form-input label
{
    font-weight: 800;
    color: gray;
}

.form-uploader
{
    display: flex;
    align-items: center;
    gap: 10px;
}

.form-uploader svg
{
    cursor: pointer;
}

.form-input input
{
    width: 90%;
    padding: 5px;
    border: none;
    border-bottom: 1px solid gray;
}

.right button
{
    width: 40%;
    padding: 10px;
    margin: 25px 0 15px 0;
    border: none;
    border-radius: 5px;
    background: #1E1E1E;
    color: #FFF;
    font-weight: bold;
    cursor: pointer;
}

.right button:hover
{
    background: rgba(0, 0, 0, 0.76);
    color: #FFF;
}