.category-slider
{
    margin-top: 250px;
    padding: 0 130px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.category-slider-container
{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.category
{
    width: 200px;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    border: 1px solid #E5E5E5;
    border-radius: 10px;
    padding: 30px;
    cursor: pointer;
}

.category span
{
    background-color: #5DC9F2;
    border-radius: 50%;
    border: 1px solid #E5E5E5;
    padding: 10px 11px;
}

.category svg
{
    width: fit-content;
    height: 50px;
    padding: 5px 10px;
    fill: #FFFFFF;
}

.category-slider-container a
{
    text-decoration: none;
    color: #000;
}