.cart
{
    position: relative;
}

.cart-container
{
    display: flex;
    justify-content: center;
    padding: 30px 90px;
    margin: 30px auto;
    gap: 30px;
}

.cart-container-top
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 7%;
}

.cart-container-top h1
{
    font-size: 25px;
    font-weight: 800;
}

.cart-container-top button
{
    padding: 3px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    border: none;
    background: gray;
    cursor: pointer;
    border-radius: 10px;
    font-size: 14px;
    color: #FFF;
}

.cart-container-top svg
{
    font-size: 25px;
    color: #FFF;
}

/**---    CART CONTAINER LEFT SECTION    ---**/
/*-------------------------------------------*/
/**   CART CONTAINER LEFT EDITION SECTION   **/
.cart-container-left
{
    width: 100%;
    border: 1px solid #47B850;
    border-radius: 20px;
}

.cart-container-right-products a
{
    text-decoration: none;
    color: #000;
}

.cart-container-left-product
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 30px;
}

.cart-container-left-product-banner-container
{
    position: relative;
}

.cart-container-left-product-banner
{
    padding: 10px 0;
    top: -30px;
    position: absolute;
    display: flex;
    align-items: center;
    width: 101%;
    border-radius: 10px;
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
}

.cart-container-left-product-banner h1
{
    font-size: 16px;
    font-weight: 500;
    color: #FFF;
    margin-left: 50px;
}

.cart-container-left-product-banner-circle
{
    left: -25px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 55px;
    height: 55px;
    background: #FFF;
    border-radius: 50%;
    margin: 0 10px;
    border: 1px solid #47B850;
}

.cart-container-left-product-banner-circle span
{
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart-container-left-product-banner-circle svg
{
    font-size: 30px;
}

.cart-container-left-product-left
{
    display: flex;
    align-items: center;
}

.cart-container-left-product-right
{
    display: flex;
    align-items: center;
    gap: 20px;
}

.cart-container-left-product-name
{
    max-width: 50%;
    word-wrap: break-word;
    font-size: 20px;
    font-weight: 800;
    margin-left: 20px;
    margin-right: 10px;
}

.cart-container-left-product-price
{
    font-weight: 800;
}

.cart-container-left-product img
{
    width: 20%;
}

.cart-container-left-product-right-delete
{
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: transparent;
    border: none;
    color: gray;
}

.cart-container-left-product-right-delete svg
{
    font-size: 25px;
}


.cart-container-left-product-right-button
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 25px;
    font-size: 20px;
    font-weight: 500;
}

.quantity-button
{
    font-family: "Red Hat Display", sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 25px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
    color: #FFF;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
}

.quantity-button:hover
{
    transform: scale(1.03);
}

/**---    CART CONTAINER RIGHT SECTION    ---**/
/*--------------------------------------------*/
/**   CART CONTAINER RIGHT EDITION SECTION   **/
.cart-container-right
{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 30%;
    width: 100%;
    border: 1px solid #47B850;
    border-radius: 20px;
}

.cart-container-right h1
{
    font-size: 20px;
    font-weight: 800;
    margin: 20px 0;
}

.cart-container-right-text
{
    width: 100%;
    font-size: 14px;
    font-weight: 500;
}

.cart-container-right-text hr
{
    margin: 0 5%;
}
.cart-container-right-text-sub
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 15px 25px;
}

.total
{
    font-size: 16px;
    font-weight: 800;
}

.cart-container-right-button
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 30px;
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
    color: #FFF;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
}