.login,
.register
{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.login-left,
.login-right,
.register-left,
.register-right
{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    padding: 5% 10%;
}

.login-left,
.register-left
{
    position: relative;
    background-image: url("../../../../public/images/reso7.jpg");
    background-size: cover;
    background-position: center;
}

.login-form-top a,
.register-form-top a
{
    text-decoration: none;
    color: inherit;
}

.login-left::before,
.register-left::before
{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.45);
}

.login-left-container,
.register-left-container
{
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.login-left-container h2,
.register-left-container h2
{
    font-size: 50px;
    font-weight: 700;
    color: #FFF;
    text-align: start;
}

.login-logo,
.register-logo
{
    display: flex;
    align-items: center;
    height: 30px;
    cursor: pointer;
    text-decoration: none;
}

.login-logo img,
.register-logo img
{
    height: 100%;
    margin-right: 5px;
}

.login-logo h1,
.register-logo h1
{
    margin-right: auto;
    color: #000;
    font-weight: 700;
    font-size: 18px;
    background: none;
}

.login-form,
.register-form
{
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.login-form-container,
.register-form-container
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
}

.login-form-title,
.login-form-input,
.login-form-button,
.register-form-title,
.register-form-input,
.register-form-button
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
}

.login-form-title,
.register-form-title
{
    margin-bottom: 10px;
}

.login-form-title h1,
.register-form-title h1
{
    font-size: 30px;
    font-weight: 800;
}

.login-form-title h3,
.register-form-title h3
{
    font-size: 14px;
    font-weight: 500;
    color: #999;
}

.login-form-input,
.register-form-input
{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.login-form-input input,
.register-form-input input
{
    padding: 10px;
    border-radius: 5px;
    border: none;
    border-bottom: 1px solid rgba(71, 184, 80, 0.3);
    outline: none;
}

.login-button
{
    width: 100%;
    padding: 10px;
    outline: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 16px;

    /* LinearBG */
    background: linear-gradient(90deg, #5DC9F2 0%, #47B850 100%);
    border-radius: 30px;
    border: none;
    color: #FFF;
    transition: transform 0.3s ease-in-out;
}

.login-button:hover
{
    transform: scale(1.05);
}

.singup-button
{
    width: 100%;
    padding: 10px;
    outline: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 16px;

    /* LinearBG */
    background: #FFF;
    border-radius: 30px;
    border: 1px solid #47B850;
    color: #47B850;
    transition: transform 0.3s ease-in-out;
}

.singup-button:hover
{
    transform: scale(1.05);
}

.login-form-options
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}

.register-form-options
{
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 0 10px;
}

.login-form-options-check,
.register-form-options-check
{
    display: flex;
    align-items: center;
    gap: 10px;
}

.options-button
{
    background: transparent;
    border: none;
    cursor: pointer;
    font-weight: 600;
    transition: transform 0.3s ease-in-out;
    text-decoration: underline;
}

.login-form-bottom,
.register-form-bottom
{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 14px;
}

.login-form-bottom button,
.register-form-bottom button
{
    background: transparent;
    border: none;
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
    transition: transform 0.3s ease-in-out;
    text-decoration: underline;
}