.brand-slider
{
    margin-top: 80px;
    padding: 0 130px;
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.brand-container
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.brand-slider img
{
    width: 100%;
    height: 150px;
    object-fit: cover;
}

.brand
{
    cursor: pointer;
}

.brand-container a
{
    color: #000;
    text-decoration: none;
}